export type SessionMethodEnum = 'IRL' | 'SKYPE';

export class Session {
  /**
   * MongoDB Object id.
   * @type {string}
   */
  _id: string;

  /**
   * The id of the expert hosting the session.
   * @type {string}
   */
  expert: string;
  title: string;
  date: Date;
  price: number;

  /**
   * Whether the session will be in real life, on Skype, etc.
   * @type {SessionMethodEnum}
   */
  method: SessionMethodEnum;

  /**
   * Only needed if the session is in real life.
   * @type {string}
   */
  address: string;

  /**
   * The maximum number of startups that can attend the session.
   * @type {number}
   */
  max_attendees: number;

  attendees: string[];
}



