import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule, JsonpModule } from '@angular/http';
import { Routes, RouterModule } from '@angular/router';

// Application Services.
import { CategoryService } from './category/category.service';
import { ExpertService } from './expert/expert.service';
import { SessionService } from './session/session.service';
import { UserService } from './user/user.service';

// Application Pipes.
import { RepeatPipe } from './pipes/repeat.pipe';
import { ContainsPipe } from './pipes/contains.pipe';

// Application Error Components.
import { HttpNotFoundComponent } from './errors/http-not-found/http-not-found.component';

// Application Components.
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { CategoryChunkComponent } from './category/category-chunk/category-chunk.component';
import { NavigationComponent } from './layout/navigation/navigation.component';
import { FooterComponent } from './layout/footer/footer.component';
import { ExpertComponent } from './expert/expert.component';
import { ExpertListComponent } from './expert/expert-list/expert-list.component';
import { SessionComponent } from './session/session.component';
import { ExpertProfileComponent } from './expert/expert-profile/expert-profile.component';
import { SessionBookComponent } from './session/session-book/session-book.component';
import { UserRegisterSessionFormComponent } from './user/user-register/user-register-session-form/user-register-session-form.component';


const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    component: HomeComponent
  },
  {
    path: 'experts',
    component: ExpertListComponent
  },
  {
    path: 'experts/:username',
    component: ExpertProfileComponent
  },
  {
    path: 'sessions/:id/book',
    component: SessionBookComponent
  },
  {
    path: '**',
    component: HttpNotFoundComponent
  }
];

@NgModule({
  declarations: [
    RepeatPipe,
    ContainsPipe,
    AppComponent,
    HomeComponent,
    CategoryChunkComponent,
    NavigationComponent,
    FooterComponent,
    HttpNotFoundComponent,
    ExpertComponent,
    ExpertListComponent,
    SessionComponent,
    ExpertProfileComponent,
    SessionBookComponent,
    UserRegisterSessionFormComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    JsonpModule,
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule],
  providers: [
    CategoryService,
    ExpertService,
    SessionService,
    UserService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



