import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import * as url from 'url';

import { Expert } from './expert';
import { environment } from '../../environments/environment';

@Injectable()
export class ExpertService {
  constructor(private http: Http) { }

  /**
   * Returns a list of Experts that belongs to the given Category.
   *
   * @param {string} category - the category's slug.
   */
  getExpertsByCategory(category: string): Observable<Expert[]> {
    return this.http.get(url.resolve(environment.server, '/experts?category=' + category))
                    .map((res: Response) => res.json() || {})
                    .catch(this.handleError);
  }

  /**
   * Returns a single Expert with the given username.
   *
   * @param {string} username - the username of the expert.
   */
  showExpertByUsername(username: string): Observable<Expert> {
    return this.http.get(url.resolve(environment.server, '/experts/' + username))
                    .map((res: Response) => res.json() || {})
                    .catch(this.handleError);
  }

  private handleError(error: Response | any) {
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }
}



