import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';

import { Expert } from '../expert';
import { ExpertService } from '../expert.service';

@Component({
  selector: 'app-expert-profile',
  templateUrl: './expert-profile.component.html',
  styleUrls: ['./expert-profile.component.scss']
})
export class ExpertProfileComponent implements OnInit {
  errorMessage: string;
  expert: Expert;

  constructor(private activatedRoute: ActivatedRoute, private expertService: ExpertService) { }

  ngOnInit() {
    // subscribe to router event
    this.activatedRoute.params.subscribe((params: Params) => {
      let username = params['username'];
      this.showExpertByUsername(username);
    });
  }

  /**
   * Load experts with the given username into the component.
   *
   * @param {string} username - the expert's username.
   */
  private showExpertByUsername(username: string) {
    this.expertService.showExpertByUsername(username)
                      .subscribe((expert) => this.expert = expert,
                                 (error) =>  this.errorMessage = <any>error);
  }
}



