import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';

import * as Moment from 'moment';

import { Session } from '../session';
import { SessionService } from '../session.service';

@Component({
  selector: 'app-session-book',
  template: require('./session-book.component.html'),
  styles: [require('./session-book.component.scss')]
})
export class SessionBookComponent implements OnInit {
  errorMessage: string;
  successMessage: string;
  session: Session;
  moment: any;

  constructor(private activatedRoute: ActivatedRoute, private sessionService: SessionService) {
    this.moment = Moment;
  }

  ngOnInit() {
    this.activatedRoute.params.subscribe((params: Params) => {
      let id = params['id'];
      this.showSession(id);
    });
  }

  onNotifySuccess(message: string) : void {
    this.successMessage = 'You have been booked!';
  }

  onNotifyError(message: string) : void {
    this.errorMessage = message;
  }

  /**
   * Loads a single Session with the given id into the component.
   *
   * @param {string} id - the id of the session.
   */
  private showSession(id: string) {
    this.sessionService.showSession(id)
                       .subscribe((session) => this.session = session,
                                  (error) =>  this.errorMessage = <any>error);
  }
}



