import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import * as url from 'url';

import { User } from '../user/user';
import { environment } from '../../environments/environment';
import { IServerResponse } from '../interfaces/server-response';

@Injectable()
export class UserService {
  constructor(private http: Http) { }

  /**
   * Stores the given user and returns the user with its id assigned.
   *
   * @param {User} user - the user to store in the database.
   * @returns {Observable<User>} the same user, with its id assigned.
   */
  storeUser(user: User) : Observable<User> {
    return this.http.post(url.resolve(environment.server, '/users'), user)
                    .map((res: Response) => res.json().user || {})
                    .catch(this.handleError);
  }


  private handleError(error: Response | any) {
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.message || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }
}



