import { Component, OnInit } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';

import * as _ from 'lodash';

import { Expert } from '../expert';
import { ExpertService } from '../expert.service';

/**
 * A listing of Experts.
 */
@Component({
  selector: 'app-expert-list',
  template: require('./expert-list.component.html'),
  styles: [require('./expert-list.component.scss')]
})
export class ExpertListComponent implements OnInit {
  errorMessage: string;
  category: string;
  experts: Expert[];

  constructor(private activatedRoute: ActivatedRoute, private expertService: ExpertService) { }

  ngOnInit() {
    // subscribe to router event
    this.activatedRoute.queryParams.subscribe((queryParams: Params) => {
      this.category = queryParams['category'];
      this.getExpertsByCategory(this.category);
    });
  }

  /**
   * Load experts into the component from the given category. Sorts it by rank.
   *
   * @param {string} category - the category id.
   */
  private getExpertsByCategory(category: string) {
    this.expertService.getExpertsByCategory(category)
                      .subscribe((experts) => this.experts = _.sortBy(experts, ['rank']),
                                 (error) =>  this.errorMessage = <any>error);
  }
}



