import { Component, OnInit } from '@angular/core';

import * as _ from 'lodash';

import { Category } from '../category';
import { CategoryService } from '../category.service';

/**
 * Displays the Categories in chunks.
 */
@Component({
  selector: 'app-category-chunk',
  templateUrl: './category-chunk.component.html',
  styleUrls: ['./category-chunk.component.scss']
})
export class CategoryChunkComponent implements OnInit {
  errorMessage: string;
  categories: Category[][];


  constructor(private categoryService: CategoryService) { }

  ngOnInit() {
    this.getCategories();
  }

  getCategories() {
    this.categoryService.getCategories()
                        .subscribe((categories) => this.categories = _.chunk(categories, 3),
                                   (error) =>  this.errorMessage = <any>error);
  }
}



