export class User {
  _id: string;
  email: string;
  password: string;

  /**
   * Display name. Used when leaving reviews, comments, etc.
   * @type {string}
   */
  name: string;

  company: string;

  /**
   * Optional description for the User's profile, which defaults to the empty string if not set.
   * @type {string}
   */
  description: string;

  /**
   * Optional image for the User's profile, which defaults to the empty string if not set.
   * @type {string}
   */
  image: string;

  /**
   * Creates a new User, optionally specifying their email, password, name, and company. Defaults to the empty string if not specified.
   *
   * @param {string} email    the email address of the user, used to login.
   * @param {string} password the password of the user.
   * @param {string} name     the real name of the user.
   * @param {string} company  the company the user represents or belongs to.
   */
  constructor(email?: string, password?: string, name?: string, company?: string) {
    this.email = email || '';
    this.password = password || '';
    this.name = name || '';
    this.company = company || '';
    this.description = '';
    this.image = '';
  }
}



