import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import 'rxjs/Rx';

import { User } from '../../user';
import { UserService } from '../../user.service'
import { Session } from '../../../session/session';
import { SessionService } from '../../../session/session.service';
import { IServerResponse } from '../../../interfaces/server-response';

/**
 * A Form that registers a user. This form is used specifically in the sessions page when booking a session. It both registers the user
 * and book them for the session at the same time.
 */
@Component({
  selector: 'app-user-register-session-form',
  template: require('./user-register-session-form.component.html'),
  styles: [require('./user-register-session-form.component.scss')]
})
export class UserRegisterSessionFormComponent implements OnInit {
  /**
   * Session id to book.
   * @type{string}
   */
  @Input() session: Session;

  /**
   * The user that will be registered.
   * @type {User}
   */
  user: User = new User();

  @Output() notifyError: EventEmitter<string> = new EventEmitter<string>();
  @Output() notifySuccess: EventEmitter<string> = new EventEmitter<string>();

  constructor(private sessionService: SessionService, private userService: UserService) { }

  ngOnInit() {
  }

  onSubmit() {
    this.userService.storeUser(this.user)
                    .subscribe((user) => {
                      this.sessionService.bookUserForSession(user, this.session)
                                         .subscribe((res) => this.handleResponse(res), (err) => this.handleError(err))
                    },
                    (err) => this.handleError(err));
  }

  private handleResponse(res: IServerResponse) : void {
    if (!res.error) {
      this.notifySuccess.emit('');
    } else {
      this.notifyError.emit(res.message);
    }
  }

  private handleError(error: any) : void {
    this.notifyError.emit(JSON.stringify(error));
  }
}



