import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import * as url from 'url';

import { Session } from './session';
import { User } from '../user/user';
import { environment } from '../../environments/environment';
import { IServerResponse } from '../interfaces/server-response';

@Injectable()
export class SessionService {
  constructor(private http: Http) { }

  /**
   * Returns a single Session with the given id.
   *
   * @param {string} id - the id of the session.
   */
  showSession(id: string): Observable<Session> {
    return this.http.get(url.resolve(environment.server, '/sessions/' + id))
                    .map((res: Response) => res.json() || {})
                    .catch(this.handleError);
  }

  /**
   * Books the given user to the given session.
   *
   * @param {User}    user    - the user booking up.
   * @param {Session} session - the session being booked for.
   * @return {Observable<boolean>} whether the booking was successful.
   */
  bookUserForSession(user: User, session: Session) : Observable<IServerResponse> {
    // TODO: Need security validation (both client and server) to ensure that the user can only sign themselves up.
    let userId = user._id;
    let sessionId = session._id;

    return this.http.put(url.resolve(environment.server, '/sessions/' + sessionId + '/signup'), { user: userId })
                    .map((res: Response) => res.json() || {})
                    .catch(this.handleError);
  }

  private handleError(error: Response | any) {
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.message || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      console.log(JSON.stringify(error));
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }
}



