import { Pipe, PipeTransform } from '@angular/core';

import * as _ from 'lodash';

/**
 * Filters an array of items, keeping only items that contains the search string. If the array of items are objects, then instead check if
 * any of the specified keys contains the search string.
 */
@Pipe({
  name: 'contains'
})
export class ContainsPipe implements PipeTransform {

  transform(items: any[], search: string, ...keys: any[]): any {
    if (search === undefined || search === '') {
      return items;
    }

    search = search.toLowerCase();

    return _.filter(items, (item) => {
      if (keys.length > 0){
        for (let key of keys) {
          if (key in item) {
            let value = item[key];
            if (_.isString(value) && value.toLowerCase().includes(search)) {
              return true;
            }
          }
        }

        return false;
      } else {
        return _.isString(item) && item.toLowerCase().includes(search);
      }
    });
  }

}



